import { GameController } from './controllers/GameController';
import { GameItem } from './models/GameItem';
import './style.css';
//tìm kiếm id app và trả về biến rootElement
const rootElement = document.querySelector('#app');
var gameApp = null; //khai báo biến gameApp có kiểu là GameController
if (rootElement) { //kiểm tra rootElement
    gameApp = new GameController([
        new GameItem(1, '', '1.png'),
        new GameItem(2, '', '2.png'),
        new GameItem(3, '', '3.png'),
        new GameItem(4, '', '4.png'),
        new GameItem(5, '', '5.png'),
        new GameItem(6, '', '6.png'),
        new GameItem(6, '', '6.png'),
        new GameItem(7, '', '7.png'),
        new GameItem(8, '', '8.png'),
        new GameItem(8, '', '11.png'),
        new GameItem(9, '', '9.png'),
        new GameItem(10, '', '10.png'),
    ], rootElement);
    gameApp.renderGameBoard(); //hiển thị thông tin của game
}
